.mobile {
	min-height: 864px;
	background: url(../img/app_bg.jpg) center center/cover no-repeat;
	padding-top: 48px;
	padding-bottom: 79px;
	.row {
		margin-top: 32px; }
	.container {
		padding: 0 90px; }
	.label {
		display: flex;
		margin: 0 auto;
		width: 112px;
		height: 24px;
		background-color: #000000;
		color: #ffffff;
		font-size: 11px;
		font-weight: 100;
		font-style: normal;
		letter-spacing: normal;
		align-items: center;
		justify-content: center;
		text-transform: uppercase; }
	.title {
		margin-top: 38px;
		color: #222222;
		font-size: 38px;
		font-weight: 700;
		font-style: normal;
		letter-spacing: normal;
		text-align: center;
		text-transform: uppercase;
		line-height: 31px; }
	.subtitle {
		margin-top: 19px;
		color: #222222;
		font-size: 17px;
		font-weight: 400;
		font-style: normal;
		letter-spacing: normal;
		text-align: center; }
	&_item {
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		position: relative;
		&_1 {
			background: url(../img/app_sheet1.jpg) center center/cover no-repeat; }
		&_2 {
			background: url(../img/app_sheet2.jpg) center center/cover no-repeat; }
		&_3 {
			background: url(../img/app_sheet3.jpg) center center/cover no-repeat; }
		&_4 {
			background: url(../img/app_sheet4.jpg) center center/cover no-repeat; }
		&_5 {
			background: url(../img/app_sheet5.jpg) center center/cover no-repeat; }
		&_6 {
			background: url(../img/app_sheet6.jpg) center center/cover no-repeat; }
		&_subtitle {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			font-style: normal;
			text-align: center;
			text-transform: uppercase; }
		&_plus {
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translate(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			border-radius: 50%;
			&:before {
				position: absolute;
				width: 14px;
				height: 2px;
				content: '';
				display: block;
				bottom: 50%;
				transform: translateY(-50%);
				left: 50%;
				transform: translateX(-50%);
				background-color: $main-color; }
			&::after {
				position: absolute;
				width: 2px;
				height: 14px;
				content: '';
				display: block;
				top: 20%;
				// transform: translateY(-50%)
				left: 50%;
				transform: translate(-50%);
				background-color: $main-color;
				transition: 0.1s all; }
			&:hover {
				&:before, &::after {
					background-color: $second-color; } } } } }


